@import 'components/variables';

.section-vuelos {
  padding: 5% 0;
  .title-page {
    font-size: 3.5rem;
    font-weight: 900;
    color: $violet;
  }
  .accordion-button:not(.collapsed) {
    color: $violet;
    font-weight: bold;
    background-color: #f0d5f8;
  }
  .accordion-button {
    font-weight: bold;
    &:focus {
      border-color: #86b7fe;
      box-shadow: none
    }
  }
  .accordion-item:last-of-type {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .clean-filtro {
    padding: 2rem;
    text-align: center;
    font-weight: bold;
    font-size: 1.3rem;
    border: 1px solid rgba(0,0,0,.125);
    border-top: 0;
    border-bottom-right-radius: .25rem;
    border-bottom-left-radius: .25rem;
    a {
      color: $text-gray;
      padding: .4rem;
      border: 2px solid $yellow;
      border-radius: .25rem;
      transition: all .3s;
      &:hover {
        color: $violet;
      }
    }
  }
  .search-more {
    position: relative;
    padding: 1rem;
    background-color: $violet;
    border-radius: 5px;
    .text-big {
      color: $white;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 0;
      line-height: 1;
    }
    a {
      color: $white;
      font-size: .8rem;
      transition: all .3s;
      &:hover {
        color: $yellow;
      }
    }
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10%;
      @media (min-width: 992px) {
        width: 30%;
      }
    }
  }
  .alarm {
    position: relative;
    padding: 1rem;
    background-color: $violet;
    border-radius: 5px;
    .text-big {
      color: $white;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 0;
      line-height: 1;
    }
    .content-alarm {
      width: 70%;
      p {
        font-size: .8rem;
      }
    }
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10%;
      @media (min-width: 992px) {
        width: 35%;
      }
    }
  }
  .nav-tabs .nav-link {
    color: $text-gray;
    font-weight: bold;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .nav-tabs .nav-link.active {
    color: $text-gray;
    font-weight: bold;
    background-color: #bfa1c7;
    border-color: #dee2e6 #dee2e6 #fff;
  }
  .nav-item {
    button {
      padding: .5rem 3rem;
      background-color: #e5d9e9;
    }
  }
  .nav-item:nth-child(2) {
    margin-left: .4rem;
  }
  .first-column {
    display: none;
    @media (min-width: 768px) {
      display: block;
    }
  }
  .column-fly {
    border: 1px solid #b1b1b1;
    padding: 0;
    .content-text-column,
    .content-text-border {
      padding: .6rem 1.3rem;
      position: relative;
    }
    .content-text-gray {
      padding: .6rem 1.3rem;
      background-color: #c4c4c4;
      .img-copa {
        width: 5%;
        @media (min-width: 768px) {
          width: 15%;
        }
      }
    }
    .content-text-border {
      border-bottom: 1px solid #b1b1b1;
    }
    .movil-type {
      border: 1px solid #7f4391;
      padding: 5px 10px;
      color: #7f4391;
      font-size: 11px;
      font-weight: 400;
      position: absolute;
      left: 20px;
      top: 5px;
      display: block;
      @media (min-width: 768px) {
        display: none;
      }
    }
  }
}